import React, { useState } from 'react';
import classes from './Projects.module.css'
import ProjectCard from './ProjectCard';
import ProjectDetail from './ProjectDetail/ProjectDetail';

export default function Projects({cardData}) {
  const [on,setOn] = useState(false);   // detailCard   state
  const [num,setNum] = useState();    // card id  state

  function showToggle(index) {    // datailCard 
    setNum(index);
    setOn(show => !show);
  }

  function moveBtn(index) {
    setNum(index);
  }

  const cards = cardData.map((aa) => {    // key   sort   
    return <ProjectCard data={aa} key={aa.id} showToggle={showToggle}/>
  })
  return (
    <>
      <div className={classes.Projects}>
        {cards}
      </div>
      <ProjectDetail showToggle={showToggle} moveBtn={moveBtn} num={num} on={on}/>
    </>
  )
}
